import 'vux/src/styles/reset.less';
import './class.scss'
import Swiper from 'vux/src/components/swiper/swiper.vue'
import Scroller from 'vux/src/components/scroller/index.vue'
import Tab from 'vux/src/components/tab/tab.vue'
import TabItem from 'vux/src/components/tab/tab-item.vue'
import Sticky from 'vux/src/components/sticky/index.vue'
import Flexbox from 'vux/src/components/flexbox/flexbox.vue'
import FlexboxItem from 'vux/src/components/flexbox/flexbox-item.vue'
import ViewBox from 'vux/src/components/view-box/index.vue'


import {fetch, rap} from 'js/fetch.js'
let url = {
list: '/merchandiseHot/list.do',
slideList: '/slide/listSlides.do'
}
url = rap(url)
import Myhead from 'components/head/head.vue'

import Top from 'components/top/top.vue'

import Foot from 'components/foot/foot.vue'

import mixin from 'js/mixin.js'

new Vue({
el: '#body',
data: {
bannerList: [{
url: 'javascript:',
img: '/static/banner.jpg',
}, {
url: 'javascript:',
img: '/static/banner.jpg',
}, {
url: 'javascript:',
img: '/static/banner.jpg',
fallbackImg: 'https://static.vux.li/demo/3.jpg'
}],
listIndex: 0,
transitionName: 'slide-left'
},
created() {

},
methods: {
show(index){
let lis = document.querySelectorAll('.bookClass li');
[].forEach.call(lis, function (item, i) {
item.className = ''
})
lis[index].className = 'selected'

if (this.listIndex > index) {
this.transitionName = 'slide-right'
} else {
this.transitionName = 'slide-left'
}
this.listIndex = index
}
},
components: {
Swiper,
Scroller,
Tab,
TabItem,
Sticky,
Flexbox,
FlexboxItem,
ViewBox
},
mixins: [mixin]
})



